            <span style="margin-left: 16px;display:inline-block;width:50px;text-align:right;"></span>
        </p>
        <p>
            <span style="display:inline-block;width:250px;text-align:right;">五险一金扣除:</span>
            <input v-model="insuranceAmount" type='number'>
            <span style="margin-left: 16px;display:inline-block;width:50px;text-align:right;"
                @click="openDrawer('insurance')">自定义</span>
        </p>
        <p>
            <span style="display:inline-block;width:250px;text-align:right;">专项附加扣除:</span>
            <input v-model="specialAmount" type='number'>
            <span style="margin-left: 16px;display:inline-block;width:50px;text-align:right;"
                @click="openDrawer('special')">自定义</span>
        </p>
        <el-drawer v-model="drawer" :title="drawerTitle" :direction="direction">
            <div v-if="drawerTitle === '五险一金'">
                <el-select v-model="insuranceType" class="m-2" placeholder="录入方式" size="large">
                    <el-option key="0" label="按缴纳金额录入" value="0" />
                    <el-option key="1" label="按缴纳基数录入" value="1" />
                </el-select>
                <div v-if="insuranceType === '0'">
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">医疗保险金额:</span>
                        <input v-model="healthInsuranceAmount" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">养老保险金额:</span>
                        <input v-model="pensionInsuranceAmount" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">失业保险金额:</span>
                        <input v-model="unemploymentInsuranceAmount" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">工伤保险金额:</span>
                        <input v-model="disabilityInsuranceAmount" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">生育保险金额:</span>
                        <input v-model="maternityInsuranceAmount" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">公积金金额:</span>
                        <input v-model="housingFundAmount" @change="insuranceChange()" type='number'>
                    </p>
                </div>
                <div v-if="insuranceType === '1'">
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">医疗保险基数:</span>
                        <input v-model="healthInsuranceBase" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">医疗保险比例:</span>
                        <input v-model="healthInsurancePercentage" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">养老保险基数:</span>
                        <input v-model="pensionInsuranceBase" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">养老保险比例:</span>
                        <input v-model="pensionInsurancePercentage" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">失业保险基数:</span>
                        <input v-model="unemploymentInsuranceBase" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">失业保险比例:</span>
                        <input v-model="unemploymentInsurancePercentage" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">工伤保险基数:</span>
                        <input v-model="disabilityInsuranceBase" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">工伤保险比例:</span>
                        <input v-model="disabilityInsurancePercentage" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">生育保险基数:</span>
                        <input v-model="maternityInsuranceBase" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">生育保险比例:</span>
                        <input v-model="maternityInsurancePercentage" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">公积金基数:</span>
                        <input v-model="housingFundBase" @change="insuranceChange()" type='number'>
                    </p>
                    <p>
                        <span style="display:inline-block;width:150px;text-align:right;">公积金比例:</span>
                        <input v-model="housingFundPercentage" @change="insuranceChange()" type='number'>
                    </p>
                </div>

            </div>
            <div v-if="drawerTitle === '专项附加'">
                <p>
                    <el-checkbox v-model="childrenEducationChecked" label="子女教育:" size="large" @change="specialChange()" />
                    <input v-model="childrenEducation" @change="specialChange()" type='number'>
                </p>
                <p>
                    <el-checkbox v-model="continuingEducationChecked" label="继续教育:" size="large"
                        @change="specialChange()" />
                    <input v-model="continuingEducation" @change="specialChange()" type='number'>
                </p>
                <p>
                    <el-checkbox v-model="medicalTreatmentChecked" label="大病医疗:" size="large" @change="specialChange()" />
                    <input v-model="medicalTreatment" @change="specialChange()" type='number'>
                </p>
                <p>
                    <el-checkbox v-model="housingLoansChecked" label="住房贷款:" size="large"
                        @change="specialChange('loans')" />
                    <input v-model="housingLoans" @change="specialChange()" type='number'>
                </p>
                <p>
                    <el-checkbox v-model="housingRentalChecked" label="住房租房:" size="large"
                        @change="specialChange('rental')" />
                    <input v-model="housingRental" @change="specialChange()" type='number'>
                </p>
                <p>
                    <el-checkbox v-model="supportingElderlyChecked" label="赡养老人:" size="large" @change="specialChange()" />
                    <input v-model="supportingElderly" @change="specialChange()" type='number'>
                </p>
            </div>
        </el-drawer>
        <p>
            <button style="margin:10px" type="button" @click="cal()">开始计算</button>
        </p>
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="index" label="月份" width="180"></el-table-column>
            <el-table-column prop="amount" label="应纳税金额" width="180"></el-table-column>
            <el-table-column prop="rate" label="税率"></el-table-column>
            <el-table-column prop="totalRate" label="往月纳税金额合计"></el-table-column>
            <el-table-column prop="rateAmount" label="本月应缴税额"></el-table-column>
            <el-table-column prop="afterAmount" label="本月税后工资"></el-table-column>
        </el-table>
    </div>
</template>
<script>
import { ref } from 'vue'
import { reactive } from 'vue'
import { ElMessage } from 'element-plus'

export default {
    name: "Home",
    components: {},
    data() {
        return {
            drawer: ref(false),
            drawerTitle: '',
            direction: ref('rtl'),
            rates: [
                [0, 0, 0],
                [36000, 0.03, 0],
                [144000, 0.1, 2520],
                [3000000, 0.2, 16920],
                [420000, 0.25, 31920],
                [660000, 0.3, 52920],
                [960000, 0.35, 85920],
                [999999999, 0.45, 181920]
            ],
            beforeAmount: 10000,
            insuranceType: '0',
            healthInsuranceAmount: 10000,
            healthInsuranceBase: 10000,
            healthInsurancePercentage: 0.02,
            pensionInsuranceAmount: 10000,
            pensionInsuranceBase: 10000,
            pensionInsurancePercentage: 0.08,
            unemploymentInsuranceAmount: 10000,
            unemploymentInsuranceBase: 10000,
            unemploymentInsurancePercentage: 0.002,
            disabilityInsuranceAmount: 10000,
            disabilityInsuranceBase: 10000,
            disabilityInsurancePercentage: 0,
            maternityInsuranceAmount: 10000,
            maternityInsuranceBase: 10000,
            maternityInsurancePercentage: 0,
            housingFundAmount: 10000,
            housingFundBase: 10000,
            housingFundPercentage: 0.12,
            insuranceAmount: 2220,
            childrenEducationChecked: ref(false),
            childrenEducation: 1000,
            continuingEducationChecked: ref(false),
            continuingEducation: 400,
            medicalTreatmentChecked: ref(false),
            medicalTreatment: 0,
            housingLoansChecked: ref(false),
            housingLoans: 1000,
            housingRentalChecked: ref(false),
            housingRental: 1500,
            supportingElderlyChecked: ref(false),
            supportingElderly: 2000,
            specialAmount: 0,
            tableData: [],
            form: reactive({
                beforeAmount: 5000,
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: '',
            })
        };
    },
    mounted() {
        this.healthInsuranceBase = this.beforeAmount;
        this.pensionInsuranceBase = this.beforeAmount;
        this.unemploymentInsuranceBase = this.beforeAmount;
        this.disabilityInsuranceBase = this.beforeAmount;
        this.maternityInsuranceBase = this.beforeAmount;
        this.housingFundBase = this.beforeAmount;
        this.healthInsuranceAmount = this.healthInsuranceBase * this.healthInsurancePercentage
        this.pensionInsuranceAmount = this.pensionInsuranceBase * this.pensionInsurancePercentage
        this.unemploymentInsuranceAmount = this.unemploymentInsuranceBase * this.unemploymentInsurancePercentage
        this.disabilityInsuranceAmount = this.disabilityInsuranceBase * this.disabilityInsurancePercentage
        this.maternityInsuranceAmount = this.maternityInsuranceBase * this.maternityInsurancePercentage
        this.housingFundAmount = this.housingFundBase * this.housingFundPercentage
        this.insuranceAmount = this.healthInsuranceAmount + this.pensionInsuranceAmount + this.unemploymentInsuranceAmount + this.disabilityInsuranceAmount + this.maternityInsuranceAmount + this.housingFundAmount
    },
    methods: {
        beforeAmountChange() {
            this.healthInsuranceBase = this.beforeAmount;
            this.pensionInsuranceBase = this.beforeAmount;
            this.unemploymentInsuranceBase = this.beforeAmount;
            this.disabilityInsuranceBase = this.beforeAmount;
            this.maternityInsuranceBase = this.beforeAmount;
            this.housingFundBase = this.beforeAmount;
            this.healthInsuranceAmount = this.healthInsuranceBase * this.healthInsurancePercentage
            this.pensionInsuranceAmount = this.pensionInsuranceBase * this.pensionInsurancePercentage
            this.unemploymentInsuranceAmount = this.unemploymentInsuranceBase * this.unemploymentInsurancePercentage
            this.disabilityInsuranceAmount = this.disabilityInsuranceBase * this.disabilityInsurancePercentage
            this.maternityInsuranceAmount = this.maternityInsuranceBase * this.maternityInsurancePercentage
            this.housingFundAmount = this.housingFundBase * this.housingFundPercentage
            this.insuranceAmount = this.healthInsuranceAmount + this.pensionInsuranceAmount + this.unemploymentInsuranceAmount + this.disabilityInsuranceAmount + this.maternityInsuranceAmount + this.housingFundAmount
        },
        insuranceChange() {
            console.info(this.insuranceType)
            if (this.insuranceType === '1') {
                this.healthInsuranceAmount = this.healthInsuranceBase * this.healthInsurancePercentage
                this.pensionInsuranceAmount = this.pensionInsuranceBase * this.pensionInsurancePercentage
                this.unemploymentInsuranceAmount = this.unemploymentInsuranceBase * this.unemploymentInsurancePercentage
                this.disabilityInsuranceAmount = this.disabilityInsuranceBase * this.disabilityInsurancePercentage
                this.maternityInsuranceAmount = this.maternityInsuranceBase * this.maternityInsurancePercentage
                this.housingFundAmount = this.housingFundBase * this.housingFundPercentage
            }
            console.info(this.healthInsuranceAmount)
            console.info(this.pensionInsuranceAmount)
            console.info(this.unemploymentInsuranceAmount)
            console.info(this.disabilityInsuranceAmount)
            console.info(this.maternityInsuranceAmount)
            console.info(this.housingFundAmount)
            this.insuranceAmount = this.healthInsuranceAmount + this.pensionInsuranceAmount + this.unemploymentInsuranceAmount + this.disabilityInsuranceAmount + this.maternityInsuranceAmount + this.housingFundAmount
            console.info(this.insuranceAmount)
        },
        specialChange(value) {
            this.specialAmount = 0;
            if ('loans' === value && this.housingLoansChecked) {
                this.housingRentalChecked = false
            }
            if ('rental' === value && this.housingRentalChecked) {
                this.housingLoansChecked = false
            }
            if (this.childrenEducationChecked) {
                this.specialAmount += this.childrenEducation;
            }
            if (this.continuingEducationChecked) {
                this.specialAmount += this.continuingEducation;
            }
            if (this.medicalTreatmentChecked) {
                this.specialAmount += this.medicalTreatment;
            }
            if (this.housingLoansChecked) {
                this.housingRentalChecked = false
                this.specialAmount += this.housingLoans;
            }
            if (this.housingRentalChecked) {
                this.housingLoansChecked = false
                this.specialAmount += this.housingRental;
            }
            if (this.supportingElderlyChecked) {
                this.specialAmount += this.supportingElderly;
            }
        },
        cal() {
            var beforeAmount = this.beforeAmount;
            var insuranceAmount = this.insuranceAmount;
            var specialAmount = this.specialAmount;
            var totalRate = 0; //缴税累计金额
            this.tableData = []; //清空计算结果
            for (var i = 1; i <= 12; i++) {
                var amount = (beforeAmount - insuranceAmount - specialAmount - 5000) * i;
                if (amount < 0) { //不用交税
                    ElMessage({
                        message: '不用交税.',
                        type: 'success',
                    })
                    return;
                }
                var rate = this.getRate(amount);
                // 本月应缴税额
                var rateAmount = Math.round((amount * rate[1]) * 100) / 100 - rate[2] - totalRate;
                //税后金额
                var afterAmount = beforeAmount - insuranceAmount - rateAmount;
                this.tableData.push({
                    index: i,
                    amount: amount.toFixed(2),
                    rate: rate[1] * 100 + "%",
                    totalRate: totalRate.toFixed(2),
                    rateAmount: rateAmount.toFixed(2),
                    afterAmount: afterAmount.toFixed(2)
                });
                totalRate = totalRate + rateAmount;
            }
        },
        getRate(value) {
            var rate = [36000, 0.03, 0];
            for (var i = 1; i < this.rates.length; i++) {
                if (value <= this.rates[0][0]) {
                    rate = this.rates[0];
                } else if (value > this.rates[i - 1][0] && value <= this.rates[i][0]) {
                    rate = this.rates[i];
                    break;
                }
            }
            return rate;
        },
        openDrawer(value) {
            this.drawer = true
            if (value === 'insurance') {
                this.drawerTitle = "五险一金"
            } else {
                this.drawerTitle = "专项附加"
            }
        }
    }
};
</script>